import { createAsyncThunk, createSlice } from "@reduxjs/toolkit"
import {
  getHomeGoodPrice,
  getHomeHighScore,
  getHomeDiscount,
  getHomeHotRecommend,
  getHomeLongfor,
  getHomePlus
} from "@/services"

const initialState = {
  goodPriceInfo: {},
  highScoreInfo: {},
  discountInfo: {},
  hotRecommendInfo: {},
  longForInfo: {},
  plusInfo: {}
}

const fetchHomeDataAction = createAsyncThunk(
  "fetchdata",
  (payload, { dispatch }) => {
    getHomeGoodPrice().then((res) => {
      dispatch(changeGoodPriceInfoAction(res))
    })

    getHomeHighScore().then((res) => {
      dispatch(changeHighScoreInfoAction(res))
    })

    getHomeDiscount().then((res) => {
      dispatch(changeDiscountInfoAction(res))
    })

    getHomeHotRecommend().then((res) => {
      dispatch(changeHotRecommendInfoAction(res))
    })

    getHomeLongfor().then((res) => {
      dispatch(changeLongforInfoAction(res))
    })

    getHomePlus().then((res) => {
      dispatch(changePlusInfoAction(res))
    })
  }
)

const homeSlice = createSlice({
  name: "home",
  initialState,
  reducers: {
    changeGoodPriceInfoAction(state, { payload }) {
      state.goodPriceInfo = payload
    },
    changeHighScoreInfoAction(state, { payload }) {
      state.highScoreInfo = payload
    },
    changeDiscountInfoAction(state, { payload }) {
      state.discountInfo = payload
    },
    changeHotRecommendInfoAction(state, { payload }) {
      state.hotRecommendInfo = payload
    },
    changeLongforInfoAction(state, { payload }) {
      state.longForInfo = payload
    },
    changePlusInfoAction(state, { payload }) {
      state.plusInfo = payload
    }
  }
})

export const {
  changeGoodPriceInfoAction,
  changeHighScoreInfoAction,
  changeDiscountInfoAction,
  changeHotRecommendInfoAction,
  changeLongforInfoAction,
  changePlusInfoAction
} = homeSlice.actions
export { fetchHomeDataAction }

export default homeSlice.reducer
