import { createAsyncThunk, createSlice } from "@reduxjs/toolkit"
import { getEntireRoomList } from "@/services/modules/entire"

const fetchRoomData = createAsyncThunk(
  "fetchRoomData",
  async (page = 0, { dispatch }) => {
    // 加载
    dispatch(changeIsLoading(true))
    // 获取当前页码
    dispatch(changeCurrentPage(page))

    const res = await getEntireRoomList(page * 20)
    dispatch(changeRoomList(res.list))
    dispatch(changeTotalCount(res.totalCount))

    // 加载结束
    dispatch(changeIsLoading(false))
  }
)

const initialState = {
  currentPage: 0, // 当前页码
  roomList: [], // 房间列表
  totalCount: 0, // 总数据个数
  isLoading: false
}

const entireSlice = createSlice({
  name: "entire",
  initialState,
  reducers: {
    changeCurrentPage(state, { payload }) {
      state.currentPage = payload
    },
    changeRoomList(state, { payload }) {
      state.roomList = payload
    },
    changeTotalCount(state, { payload }) {
      state.totalCount = payload
    },
    changeIsLoading(state, { payload }) {
      state.isLoading = payload
    }
  }
})

export const {
  changeCurrentPage,
  changeRoomList,
  changeTotalCount,
  changeIsLoading
} = entireSlice.actions
export { fetchRoomData }

export default entireSlice.reducer
