import styled from "styled-components"

export const RightWrapper = styled.div`
  display: flex;
  flex: 1;
  margin-right: 10px;
  justify-content: flex-end;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  color: ${(props) => props.theme.text.primaryColor};

  .btns {
    display: flex;
    box-sizing: content-box;
    .btn {
      height: 18px;
      line-height: 18px;
      padding: 12px 15px;
      box-sizing: content-box;
      cursor: pointer;
      border-radius: 22px;

      &:hover {
        background-color: #f5f5f5;
      }
    }
  }

  .profile {
    display: flex;
    margin-right: 24px;
    width: 77px;
    height: 42px;
    color: ${(props) => props.theme.text.primaryColor};

    justify-content: space-evenly;
    align-items: center;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 25px;
    background-color: #fff;
    cursor: pointer;

    /* 阴影动画效果 */
    ${(props) => props.theme.mixin.boxShadow}

    .panel {
      position: absolute;
      top: 70px;
      right: 40px;
      width: 240px;
      background-color: #fff;
      border-radius: 10px;
      box-shadow: 0 0 4px rgba(0, 0, 0, 0.18);

      .top,
      .bottom {
        padding: 10px 0;
      }

      .top {
        border-bottom: 1px solid #ddd;
      }

      .item {
        height: 40px;
        line-height: 40px;
        padding: 0 16px;

        &:hover {
          background-color: #f5f5f5;
        }
      }
    }
  }
`
