import React, { memo, useEffect, useState } from "react"
import { RightWrapper } from "./styled"
import IconInternet from "@/assets/img/svg/icon_internet"
import IconMenu from "@/assets/img/svg/icon_menu"
import IconAvatar from "@/assets/img/svg/icon_avatar"

const HeaderRight = memo(() => {
  const [isShowPanel, setIsShowPanel] = useState(false)
  useEffect(() => {
    function handleWindowClick() {
      setIsShowPanel(false)
    }
    window.addEventListener("click", handleWindowClick, true)

    return () => {
      // 取消监听
      window.removeEventListener("click", handleWindowClick, true)
    }
  }, [])
  return (
    <RightWrapper>
      <div className="btns">
        <span className="btn">登录</span>
        <span className="btn">注册</span>
        <span className="btn">
          <IconInternet />
        </span>
      </div>
      <div className="profile" onClick={(e) => setIsShowPanel(true)}>
        <IconMenu />
        <IconAvatar />

        {isShowPanel && (
          <div className="panel">
            <div className="top">
              <div className="register item">注册</div>
              <div className="login item">登录</div>
            </div>
            <div className="bottom">
              <div className="item">出租房源</div>
              <div className="item">开展体验</div>
              <div className="item">帮助</div>
            </div>
          </div>
        )}
      </div>
    </RightWrapper>
  )
})

export default HeaderRight
