import nprogress from "nprogress"
import "nprogress/nprogress.css"

nprogress.inc(0.2)

// nprogress.set(0.4)
nprogress.configure({
  easing: "ease",
  speed: 300,
  showSpinner: false
})

function showFullLoading() {
  nprogress.start()
}

function hideFullLoading() {
  nprogress.done()
}

export { showFullLoading, hideFullLoading }
