import styled from "styled-components"

export const HeaderWrapper = styled.div`
  ${(props) => (props.isFixed ? props.theme.mixin.fixed : "")}

  z-index: 9;
  border-bottom: 1px solid #eee;

  .content {
    background-color: #fff;
    position: relative;
    z-index: 19;
    .top {
      display: flex;
      align-items: center;
      height: 80px;
    }

    .search-area {
      height: 100px;
    }
  }

  .cover {
    position: fixed;
    z-index: 9;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
  }
`
